.linksContainer {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin: 50px 0;
    width: 100%;
    text-align: center;
}

.resume {
    flex: 4;
    text-align: left;
}

.link { flex: 1 }

.divider { flex: .3 }

@media only screen and (max-width: 600px)
{
    .linksContainer{ flex-direction: column }   
    .resume{
        flex: 1;
        text-align: center;
    } 
    .link{ padding-top: 20px; }
    .divider{ display: none; }
}
